    display: block;
    width: 100px;
    height: 100px;
    background: url(data:image/png;base64,) no-repeat;
    -webkit-animation: rotateAnim 1.5s linear infinite;
            animation: rotateAnim 1.5s linear infinite;
    background-size: 100%;
    margin-left: 49%;
